@import url(https://fonts.googleapis.com/css?family=Work+Sans:400,500,700,900);

body{margin:0;padding:0;background-color:#F8E0FF;font-family:'Work Sans', sans-serif;color:#5C00E6;font-size:18px}a{color:#5C00E6}h2{font-size:20px}.admin-nav{display:flex;justify-content:center;margin-top:40px}.admin-nav div:hover{cursor:pointer}.admin-nav div:first-of-type{margin-right:5px}.admin-nav div:nth-of-type(2){margin-left:5px}.admin-nav .selected{text-decoration:underline}#title{margin:3px}h1{font-size:70px}@media (max-width: 500px){h1{font-size:50px}}#content{text-align:center;margin:0 15px}input{background-color:rgba(0,0,0,0);border:0px solid #5C00E6;border-bottom-width:5px;-webkit-appearance:none;border-radius:0px;font-size:40px;color:#5C00E6;font-family:'Work Sans', sans-serif;font-weight:500;display:block;width:100%;margin:50px auto;text-align:center}@media (max-width: 500px){input{font-size:30px;margin:30px auto;max-width:347px}}input:focus{outline:none}input#submit{margin-top:20px}.del{display:none;height:0px}.input-wrapper{width:100%;max-width:450px;margin:auto}.input-wrapper.show-ex .del{display:inline-block;position:relative;float:right;top:-110px;font-size:22px;cursor:pointer}.ex{font-size:40px;text-align:center;margin-bottom:0px}@media (max-width: 500px){.ex{font-size:30px}}#submit{border-width:0px;background-color:#F8E0FF;border:2px solid #5C00E6;font-size:20px;padding:10px 12px;display:block;cursor:pointer;width:auto;color:#5C00E6;clear:both;margin:auto}.results{max-width:600px;margin:auto;padding:10px}ul{list-style-type:none;padding-left:0px}ul li h2{display:inline}ul li.movie,ul li.tv,ul li.actor{margin-bottom:20px}ul li.movie.sub,ul li.tv.sub,ul li.actor.sub{margin-bottom:0px}ul li.movie:before{content:'üé•';display:inline;margin-right:8px}ul li.tv:before{content:'üì∫';display:inline;margin-right:8px}ul li.actor:before{content:'‚≠êÔ∏è';display:inline;margin-right:8px}ul li ul{margin:6px 0px 10px;padding-left:29px}.loader{display:flex;flex-direction:column;align-items:center;font-size:26px;justify-content:center;margin-top:95px}.loader .lds-ellipsis{display:inline-block;position:relative;width:73px;height:80px}.loader .lds-ellipsis div{position:absolute;top:20px;width:10px;height:10px;border-radius:50%;background:#5C00E6;animation-timing-function:cubic-bezier(0, 1, 1, 0)}.loader .lds-ellipsis div:nth-child(1){left:8px;animation:lds-ellipsis1 0.6s infinite}.loader .lds-ellipsis div:nth-child(2){left:8px;animation:lds-ellipsis2 0.6s infinite}.loader .lds-ellipsis div:nth-child(3){left:32px;animation:lds-ellipsis2 0.6s infinite}.loader .lds-ellipsis div:nth-child(4){left:56px;animation:lds-ellipsis3 0.6s infinite}@keyframes lds-ellipsis1{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes lds-ellipsis3{0%{transform:scale(1)}100%{transform:scale(0)}}@keyframes lds-ellipsis2{0%{transform:translate(0, 0)}100%{transform:translate(24px, 0)}}

